.overlay {
    position: absolute;
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s ease;
    overflow: hidden;
  }

  .overlayVisible {
    visibility: visible;
    opacity: 1;

    .cart {
        transform: translateX(0%);
    }
}
  
  .cart {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    background: #ffffff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    transform: translateX(100%);
    transition: all 0.3s ease;
  
    .items {
      flex: 1;
      overflow: auto;
      margin-bottom: 40px;
    }
  
    h2 {
      margin: 0;
    }

    @media screen and(max-width: 500px){
      width: 95%;
    }
  }