body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(74, 43, 166);
  background: linear-gradient(90deg, rgba(74, 43, 166, 1) 0%, rgba(130, 84, 220, 1) 35%);
}

* {
  font-family: 'Inter', system-ui;
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #eaeaea;

  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }

  .clear {
    position: absolute;
    right: 5px;
    width: 20px;
    height: 20px;
    top: 13px;
  }
}

.products {
  display: flex;
  flex-wrap: wrap;
}

.cartTotalBlock {
  ul {
    display: block;
    margin-bottom: 40px !important;

    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        position: relative;
        top: -4px;
        margin: 0 7px;
      }
    }
  }

  .mainButton {
    position: relative;

    &:disabled {
      background-color: #bebebe !important;
      cursor: default !important;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }

    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.mainButton {
  width: 100%;
  height: 55px;
  background: #8254dc;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;

  &:disabled {
    animation: button-loading 0.7s ease-in-out infinite;
  }

  &:hover {
    background: darken(#8254dc, 5%);
  }

  &:active {
    background: lighten (#8254dc, 5%);
  }
}

.cartItem {
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;

  .cartItemImg {
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }

  p {
    font-size: 16px;
    margin: 0;

    @media screen and(max-width: 500px) {
      font-size: 14px;
    }
  }

  b {
    font-size: 14px;
  }

  .removeBtn {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover {
      opacity: 1;
    }
  }
}


.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .mainButton {
    width: 245px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;

    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }

    img {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.7;
  }

  100% {
    opacity: 1;
  }
}

@media screen and(max-width: 650px) {
  .header {
    flex-direction: column;
  }

  .nav {
    margin-top: 40px !important;
    align-self: end;
  }

  .content__top {
    flex-direction: column;
  }

  .search-block {
    margin-top: 30px;

    input {
      width: 100%;
    }
  }
}

.slick-prev:before,
.slick-next:before {
  color: #C8C8C8 !important;
}